import React,{useEffect} from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import './MeetWithClub.scss'
import { Backdrop, Box, Fade, Modal } from '@mui/material'
import { Swiper, SwiperSlide } from "swiper/react"
import {Autoplay, Pagination } from "swiper"
import video from '../../../assets/videos/metaverse.mp4'


const Cities = [
  {
    name: 'Tokyo, Japan',
    image: 'tokyo.webp',
  },
  {
    name: 'London, UK',
    image: 'london.webp',
  },
  {
    name: 'New York, USA',
    image: 'newyork.webp',
  },
  {
    name: 'Hollywood, USA',
    image: 'hollywood.webp',
  },
  {
    name: 'Seoul, South Korea',
    image: 'seoul.webp',
  },
]

const MeetWithClub = () => {
  const [open, setOpen] = React.useState(false)
  const handleOpen = () => setOpen(true)
  const handleClose = () => setOpen(false)
  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width:'auto',
    bgcolor: 'transfarent',
    boxShadow: 24,
    p: 4,
  }
  useEffect(()=>{

    const display=document.getElementsByClassName('blink-text')
    for(let i=0; i<display.length; i++){
      let currentDisplay=display[i]
      const revealText=()=>{
        currentDisplay.style.opacity=1
        setTimeout(()=>{
          currentDisplay.style.opacity=0
        },[3000])
      }
      revealText()
      setInterval(()=>{
        
        revealText()
       
      },[3500])
    }
   
  },[])
  return (
    <div>
      <Container>
        <div className='king-Nightclub-wraper p-2 p-md-5 meetClub'>
          <Row className='reverse-section'>
            <Col className='col-12 col-md-5 p-5 pt-md-2 king-logo pb-0'>
              <img className='img-fluid meetimg' src="/images/night-king.png" alt="" />
            </Col>
            <Col className='col-12 col-md-7 ps-md-3 p-4 p-md-1 pe-md-3 pb-0'>
              <div className='kingClub-text text-start '>
                <h2>Meet <span className='text-yellow '>Night King Club’s</span> visionary leader</h2>
                <div className='pb-2'>
                  <img className='img-fluid meetimg-mobile' src="/images/night-king.png" alt="" />
                </div>
                <p className='pt-2'>
                  Night King Club formed its initial roots through a visionary leader, Night King, who built his fortune through successful real estate and crypto investments. He attributes his success to 
                  <span className='text-yellow'> hanging out with the right people and taking actions at the right moments.</span>
                  <br/>
                  <br/>
                  With his deep connections in the entertainment and fashion industries, Night King has hosted some of the 
                  <span className='text-yellow'> most sought-after parties worldwide. </span>
                  His life of adventure, top business connections and links to beautiful people is the envy of others.
                  <br/>
                  <br/>
                  Night King and his friends achieved financial freedom early in their careers. Their next ambitious goal? It’s to build the most influential NFT community by
                  <span className='text-yellow'> providing extraordinary life experiences to the best people in Web3. </span>
                  <br/>
                  <br/>
                  <br/>
                </p>
              </div>
            </Col>
          </Row>
          <div className='blink-text '>Are you ready for your next adventure? </div>
       
        </div>
        <Container className="pb-5 ">
          <div className='primary-text-2 px-md-5'>
            <h2 className='py-4'><span className="text-yellow">Have fun with fellow NKG Clubbers around the world</span></h2>
            <p className="text-align-left">
              Night King Club will hold exclusive luxurious parties along with major Crypto/NFT events (e.g., NFT NYC), so you can enjoy the great experience during the same trip. 
              <br/>
              <br/>
              With our deep connections with
              <span className="text-yellow"> international modeling agencies and professional event planning teams, </span> 
              we can ensure your time is spent
              <span className="text-yellow"> ONLY at the best-in-class parties containing the most fabulous people.</span> 
              <br/>
              <br/>
              Besides the major parties, we are also partnering with high-end venues in major cities worldwide to 
              <span className="text-yellow"> offer our members exclusive VIP benefits. </span> 
              Simply shoot a message in our NFT-gated Discord group when you arrive in a new city. Then, you can
              <span className="text-yellow"> hang out with fellow NKG Clubbers at these amazing venues </span> 
              to chat about the future of Web3.
           </p>
          </div>
          {/* <Col className="pb-5">
            { Cities.map((city) => {
              return (
                <div>
                  <img className="city-image pt-4" src={`images/cities/${city.image}`} alt={city.image} />
                  <h1 className="pt-1 text-yellow">{city.name}</h1>
                </div>
              )
            })}
          </Col> */}
          <Swiper 
            autoplay={{
              delay: 7000,
              disableOnInteraction: false,
            }}
            pagination={{
              clickable: true,
            }}
            modules={[Autoplay, Pagination]}
            // className="mySwiper"
          >
            {Cities.map((city,index) => {
              return (
                <SwiperSlide key={index}>
                  <Row className='m-0 p-3 p-md-5 '>
                    <div className='city-wrapper'>
                      <img className="city-image pt-4" src={`images/cities/${city.image}`} alt={city.image} />
                      <h1 className="pt-1 text-yellow">{city.name}</h1>
                    </div>
                  </Row>
                </SwiperSlide>
              )
            })}            
          </Swiper>
        </Container>
        <Container>
          <div className='video-wraper'>
            {/* <div className='modal-video'>
              <img className='img-fluid' src="/images/metaverse-thumbnail.webp" alt="" />
              <button onClick={()=>handleOpen()} className='play-thevideo-btn'><i class="fa-solid fa-play play-icon"></i></button>
            </div> */}
            <div className='modal-video'>
              <video preload='none' controls autoPlay playsInline muted loop src={video}/>
            </div>
          </div>
          <Modal
            className="modal-video"
            aria-labelledby="transition-modal-title"
            aria-describedby="transition-modal-description"
            open={open}
            onClose={handleClose}
            closeAfterTransition
            BackdropComponent={Backdrop}
            BackdropProps={{
              timeout: 500,
            }}
          >
            <Fade in={open}>
              <Box sx={style}>
                <iframe width="560" height="315" src="https://www.youtube.com/embed/1hNdsrJgYHs?controls=0" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
              </Box>
            </Fade>
          </Modal>
          <div className='primary-text '>
            <h2 className='mx-md-5 py-4 mt-5 pt-4'><span className="text-yellow">NKG Metaverse: Redefining Web3 high-end networking</span></h2>
            <p className='px-md-5  pb-4 text-align-left'>
              Besides meeting others IRL and vibing at some of the most exclusive parties, we are also building a metaverse for high-end virtual networking. Here, you will be able to meet new acquaintances, play exciting games and unwind with your friends at various luxurious nightclub scenes. 
            </p>
          </div>
        </Container>
      </Container>
    </div>
  );
};

export default MeetWithClub;